import React from 'react'
import { graphql } from 'gatsby'
import { GatsbyImage } from "gatsby-plugin-image";
import Layout from "/src/layouts/main/layout/layout.js";
import Seo from "/src/components/seo"

import "/src/assets/scss/templates/cheatsheet.scss"

const ArticleTemplate = ({ data }) => {
  const  article = data.markdownRemark
  /* Usar imagen en el top con parallax */
  const { description, title, frontimage, date } = article.frontmatter
  const { slug } = article.fields
  const content = article.html
  
    console.log(frontimage)
  return (
    <Layout>
      <div className="cheatsheet-content">
        <Seo />
        <h1>{title}</h1>
        {/* <p>by <Link to={`/authors/User_${data.strapiArticle.author ? data.strapiArticle.author.id : ''}`}>{data.strapiArticle.author ? data.strapiArticle.author.username : 'An√≥nimo'}</Link></p> */}
        
        { frontimage ? <GatsbyImage image={frontimage.childImageSharp?.gatsbyImageData} alt="" className="cheatsheets-item__img" /> : '' }
        <section className='markdown-body' 
            dangerouslySetInnerHTML={{ __html: content }}
        ></section>
      </div>
    </Layout>
  )
  
}

export default ArticleTemplate

export const query = graphql`query CheatsheetBySlug(
    $id: String!
    $previousPostId: String
    $nextPostId: String
  ) {
    site {
      siteMetadata {
        title
      }
    }
    markdownRemark(
        id: { eq: $id }
      ) {
      id
      excerpt(pruneLength: 160)
      html
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
        frontimage {
            childImageSharp {
                gatsbyImageData(width: 960, height: 300, layout: CONSTRAINED)
            }
          }
        description
      }
      fields {
        slug
      }
    }
    previous: markdownRemark(id: { eq: $previousPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
    next: markdownRemark(id: { eq: $nextPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
  }
`